import * as Yup from 'yup';
import PropTypes from 'prop-types';
import { useMemo, useEffect } from 'react';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// swal
import swal from 'sweetalert';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Alert, Stack } from '@mui/material';
import axios from '../../utils/axios';
import { FormProvider, RHFTextField } from '../../components/hook-form';

// drop dwon style
import useIsMountedRef from '../../hooks/useIsMountedRef';
// ----------------------------------------------------------------------
Adexint.propTypes = {
    discount: PropTypes.object,
};
export default function Adexint({ discount }) {
    const isMountedRef = useIsMountedRef();
    const NewUserSchema = Yup.object().shape({
    });
    const defaultValues = useMemo(
        () => ({
            adex1_username: discount?.adex1_username,
            adex1_password: discount?.adex1_password,
            adex2_username: discount?.adex2_username,
            adex2_password: discount?.adex2_password,
            adex3_username: discount?.adex3_username,
            adex3_password: discount?.adex3_password,
            adex4_username: discount?.adex4_username,
            adex4_password: discount?.adex4_password,
            adex5_username: discount?.adex5_username,
            adex5_password: discount?.adex5_password
        }),
        [discount]
    );
    const AccessToken = window.localStorage.getItem('accessToken');
    const methods = useForm({
        resolver: yupResolver(NewUserSchema),
    });
    useEffect(() => {
        if (discount !== undefined) {
            reset(defaultValues);
        }

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [discount]);
    const {
        reset,
        setError,
        handleSubmit,
        formState: { errors, isSubmitting },
    } = methods;

    const onSubmit = async (data) => {
        try {
            await axios.post(`/api/edit/adexapi/charges/account/${AccessToken}/habukhan/secure`, data)
            swal('success', `Updated`, 'success');
        } catch (error) {
            if (isMountedRef.current) {
                setError('afterSubmit', error);
            }
        }
    };

    return (
        <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
            <Grid container spacing={3}>

                <Grid item xs={12} md={8}>
                    {!!errors.afterSubmit && <Alert severity="error">{errors.afterSubmit.message}</Alert>}
                    <Card sx={{ p: 3 }}>

                        <Box
                            sx={{
                                display: 'grid',
                                columnGap: 3,
                                rowGap: 3,
                                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(2, 1fr)' },
                            }}
                        >
                            <RHFTextField name="adex1_username" label="Adex 1 USERNAME" autoComplete="username" InputLabelProps={{ shrink: true }} />
                            <RHFTextField name="adex1_password" type="password" autoComplete="new-password" label="Adex 1 PASSWORD" InputLabelProps={{ shrink: true }} />
                            <RHFTextField name="adex2_username" label="Adex 2 USERNAME" autoComplete="username" InputLabelProps={{ shrink: true }} />
                            <RHFTextField name="adex2_password" type="password" autoComplete="new-password" label="Adex 2 PASSWORD" InputLabelProps={{ shrink: true }} />
                            <RHFTextField name="adex3_username" label="Adex 3 USERNAME" autoComplete="username" InputLabelProps={{ shrink: true }} />
                            <RHFTextField name="adex3_password" type="password" autoComplete="new-password" label="Adex 3 PASSWORD" InputLabelProps={{ shrink: true }} />
                            <RHFTextField name="adex4_username" label="Adex 4 USERNAME" autoComplete="username" InputLabelProps={{ shrink: true }} />
                            <RHFTextField name="adex4_password" type="password" autoComplete="new-password" label="Adex 4 PASSWORD" InputLabelProps={{ shrink: true }} />
                            <RHFTextField name="adex5_username" label="Adex 5 USERNAME" autoComplete="username" InputLabelProps={{ shrink: true }} />
                            <RHFTextField name="adex5_password" type="password" autoComplete="new-password" label="Adex 5 PASSWORD" InputLabelProps={{ shrink: true }} />

                        </Box>
                        <Stack alignItems="flex-end" sx={{ mt: 3 }}>
                            <LoadingButton fullWidth size="large" type="submit" variant="contained" loading={isSubmitting}>
                                Proceed
                            </LoadingButton>
                        </Stack>
                    </Card>
                </Grid>
            </Grid>
        </FormProvider>
    );
}
